body {
  font: 14px "Century Gothic", Futura, sans-serif;
  margin: 20px;
}

ol, ul {
  padding-left: 30px;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.status {
  margin-bottom: 10px;
}

.square {
  background: #fff;
  border: 1px solid #999;
  float: left;
  font-size: 24px;
  font-weight: bold;
  line-height: 34px;
  height: 34px;
  margin-top: -1px;
  margin-right: -1px;
  padding: 0;
  text-align: center;
  width: 34px;
}

.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  /*background: #ddd;*/
}

.game {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
}

.game-info {
  margin-left: 20px;
}

/*
        .gridMain{
            display: grid ;
            grid-template-columns: auto auto;
            grid-template-rows: 80vh;
        }
        .gridMain > div {
            padding-left: 5px;
            padding-right: 5px;
        }

        .Left{ grid-column: 1/ span 1;}
        .Right{ grid-column: 2/ span 1; }
        .Top{ grid-row: 1/ span 1; }
        .Bottom{ grid-row: 2/ span 1; }
    */

#gridMain,
#gridMain table td,
#gridMain table th{font-size: 16px !important;}
#gridMain { display: -webkit-flex; display: flex;  -webkit-flex-wrap: nowrap;  flex-wrap: nowrap; width: 80vw  }
#gridMain > div { height: 80vh;}
#map { overflow: auto; width: 80vh; }

#RightDiv > div > div { height: 30vh !important; }

#moveBtnDiv button,
#saveBtnDiv button{ margin: 5px; }
#moveBtnDiv button img,
#saveBtnDiv button img{ width: 20px; height: 20px; padding: 5px; }

#infoButton { -webkit-align-self: end; align-self: end; border: black 1px dashed; margin: 5px }
#infoButton span{ font-size: medium; font-size: initial; padding: 5px; padding-right: 0 }
#infoButton img{ width: 20px; height: 20px; padding: 5px }

#tankDiv img { width: 100px; height: 100px; }

#commandsDiv { overflow: auto; height: 20vw; min-width: 250px}

#commands th{ position: -webkit-sticky; position: sticky; top: 0;}
#commands td,
#commands th{ text-align: center; background: lightgray; border:  2px solid black  }

#commands tr > td:nth-child(1){ width: 20px}
.gameTD
{
    padding: 5px ;
    width: 50px ;
    height: 50px ;
}

#gameButtonsDiv{ overflow: auto; min-width: 232px; }

#gameButtonsDiv > div {}
#gameButtonsHead{ width: 100%; }
#gameButtonsHead > button {padding: 5px}

#gameButtons { display: -webkit-flex; display: flex; -webkit-flex-wrap: wrap; flex-wrap: wrap; -webkit-justify-content:space-evenly; justify-content:space-evenly }

.driver.selected{ border: #F00 solid !important; }
.gunner.selected{ border: #0F0 solid !important; }
.charger.selected{ border: #00F solid !important; }

.flex-column{ display: -webkit-flex; display: flex; -webkit-flex-direction: column; flex-direction: column; -webkit-align-items: center; align-items: center}
.flex-row{ display: -webkit-flex; display: flex; -webkit-flex-direction: row; flex-direction: row; -webkit-align-items: center; align-items: center}
.flex-justify-start {-webkit-justify-content: flex-start;justify-content: flex-start;}
.flex-justify-center {-webkit-justify-content: center;justify-content: center;}
.flex-justify-evenly {-webkit-justify-content:space-evenly;justify-content:space-evenly;}
.flex-align-start {-webkit-align-items: flex-start;align-items: flex-start;}

.padding-5 {padding: 5px;}
.margin-1 {margin: 1px;}
.margin-5 {margin: 5px;}

.widthAll { width: 100%; }
.overflow { overflow: auto}
::-webkit-scrollbar-button {
    background-image:url('');
    background-repeat:no-repeat;
    width:6px;
    height:0px
}

::-webkit-scrollbar-track {
    background-color:#32312e;
    box-shadow:0px 0px 3px #000 inset;
}

::-webkit-scrollbar-thumb {
    border-radius: 5px;
    background-color:#ffcb17;
    box-shadow:0px 1px 1px #fff inset;
    background-image:url('https://yraaa.ru/_pu/24/59610063.png');
    background-position:center;
    background-repeat:no-repeat;
}

::-webkit-resizer{
    background-image:url('');
    background-repeat:no-repeat;
    width:7px;
    height:0px
}

::-webkit-scrollbar{
    width: 11px;
}
